<template>
    <h2>Buscar Numero</h2>
  <form @submit.prevent="getNu">
    <input class="form-control" type="text" v-model="id">
    <button class="btn btn-primary form-control" type="submit">
        Buscar
    </button>
  </form>

  <etiquetaNumero class="etiquetaNumero "
      :numero = "getNum.numero"
      :ocupado = "getNum.ocupado"
      />

</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
    data(){
        return{
            id:''
        }
    },
    computed:{
        ...mapGetters('numeros',['getNum']),
    },
    components:{
        etiquetaNumero : defineAsyncComponent(()=> import('./etiquetaNumero.vue'))
    },
    methods:{
        ...mapActions('numeros',['getNumero']),
        ...mapMutations('numeros',['setNumero']),
        
        getNu(){
            this.getNumero(this.id)
        }
    },
    unmounted(){
        this.setNumero({})
    }
}
</script>

<style scoped>

    .etiquetaNumero{
        margin-left: 45%;
    }

    form{
        margin-left: 15%;
        width: 70%;
    }
</style>